@import url('https://fonts.googleapis.com/css2?family=Nunito+Sans:opsz,wght@6..12,300;6..12,400;6..12,500;6..12,600;6..12,700;6..12,800&display=swap');
body {
  margin: 0;
  font-family: 'Nunito Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #F5F6FA !important;
}

code {
  font-family: 'Nunito Sans', sans-serif;
}


*{
  margin: 0px;
  padding: 0px;
}

.link{
  text-decoration: none;
  color: inherit;
}

.loginForm-container {
  background-image: url('Images/team-leader-teamwork-concept_74855-6671-removebg-preview 1.png');
  background-color: #1976d2;
  background-size: cover;
  background-position: center;
  min-height: 100vh; /* Minimum height to cover the entire viewport */
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}
.Notfound-container {
  background-image: url('Images/team-leader-teamwork-concept_74855-6671-removebg-preview 1.png');
  background-color: #1976d2;
  background-size: cover;
  background-position: center;
  min-height: 100vh; /* Minimum height to cover the entire viewport */
  display: flex;
  /* align-items: center; */
  justify-content: center;
  width: 100%;
}

.login-form{
  padding: 10px 15px 20px 15px;
}
.p-5px{
  padding: 5px;
}

.Mui-error{
  margin: 0 !important;
}

.css-108bkvk-MuiDrawer-docked {
  width: 30px !important;
}

.Icon-bg-primary-white{
  background-color: #fff;
}

.badge span{
  background-color: #fff;
  color: #568aff;
}

.profileMatch{
  padding: 10px 16px 0px 16px;
}

.profile-fileUpload{
  padding: 0 16px;
}
.fileUpload div{
  padding-top: 25px !important;
}

.button-t {
  border-radius: 5px;
  font-size: 18px;
  background-color: #1976d2;
  color: #fff;
  width: 130px;
  height: 40px;
}
.bg-primary{
  background-color: #1976d2 !important;
  color: #fff;
}

.button-a:hover{
  background-color: #1976d2 !important;
  
}
.button-a:hover svg {
  color: #fff !important;
}

.text-size{
  font-size: 18px;
  line-height: 19.1px;
  font-weight: 700;
}
.MuiDialog-container .MuiTypography-root{
  padding: 5px 24px;
}

.upload-sec .MuiBox-root{
  width: 185px;
}
.upload-sec .MuiBox-root:hover{
  background-color: #F5F6FA;
  cursor: pointer;
}

.MuiDataGrid-columnHeader:hover svg {
color: #fff;
}

@media (max-width: 600px) {
  main {
    width: 100%;
  }
}
@media (min-width: 601px) and (max-width: 900px) {
  main {
    width: 100%;
  }
}
@media (min-width: 901px) {
  main {
    width: 1200px;
  }
}